
export async function fetchLoggedInUserOrders(userId) {
  try {
    const response = await fetch(`http://localhost:8080/orders/?user=${userId}`);
    if (!response.ok) {
      throw new Error('Network response was not ok');
    }
    const data = await response.json();
    console.log(data);
    return { data };
  } catch (error) {
    console.error('Error fetching the logged-in user orders:', error);
    return { error: error.message };
  }
}


export async function fetchLoggedInUser(userId) {
  try {
    const response = await fetch(`http://localhost:8080/users/${userId}` );
    if (!response.ok) {
      throw new Error('Network response was not ok');
    }
    const data = await response.json();
    console.log(data);
    return { data };
  } catch (error) {
    console.error('Error fetching the logged-in user:', error);
    return { error: error.message };
  }
}







export function updateUser(update) {
  return new Promise(async (resolve) => {
    const response = await fetch('http://localhost:8080/users/'+update.id, {
      method: 'PATCH',
      body: JSON.stringify(update),
      headers: { 'content-type': 'application/json' },
    });
    const data = await response.json();
    // TODO: on server it will only return some info of user (not password)
    resolve({ data });
  });
}

